<template>
    <div id="app">
        <van-nav-bar title="跑者信息"/>
        <van-cell-group>
            <van-field
                v-model="username"
                clearable
                label="姓名"
                placeholder="请输入姓名"
            />
            <van-cell title="性别" is-link value="请选择" @click="selectXb"/>
            <van-cell title="出生日期" is-link value="请选择" @click="selectCsrq"/>
            <van-field
                v-model="username"
                clearable
                label="手机号"
                placeholder="请输入手机号"
            />
            <van-field
                v-model="username"
                clearable
                label="邮箱"
                placeholder="请输入邮箱"
            />
            <van-cell title="证件类型" is-link value="请选择" />
            <van-field
                v-model="username"
                clearable
                label="证件号"
                placeholder="请输入证件号"
            />
            <van-field
                v-model="username"
                clearable
                label="紧急联系人"
                placeholder="请输入证紧急联系人"
            />
            <van-field
                v-model="username"
                clearable
                label="紧急联系人电话"
                placeholder="请输入紧急联系人电话"
            />
            <van-field
                v-model="username"
                clearable
                label="紧急联系人关系"
                placeholder="请输入紧急联系人关系"
            />
            <van-cell title="血型" is-link value="请选择" />
            <van-cell title="T恤尺码" is-link value="请选择" />
            <van-cell>
                <van-button size="large" type="primary">提交</van-button>
            </van-cell>
        </van-cell-group>
        <van-datetime-picker
            v-show="birthPicker"
            v-model="brithdate"
            type="date"
            :formatter="formatter"
        />
        <van-picker :columns="pickColumns.xb" @change="onChangeXb" v-show="xbPicker"/>
    </div>
</template>
<script>
    export default {
        name: 'Layout',
        data () {
            return {
                birthPicker: false,
                xbPicker: false,
                username: '',
                xb: '',
                brithdate: new Date(),
                pickColumns: {
                    xb: ["男", "女"]
                }
            }
        },
        methods: {
            formatter(type, value) {
                if (type === 'year') {
                    return `${value}年`;
                } else if (type === 'month') {
                    return `${value}月`
                } else {
                    return `${value}日`
                }
                return value;
            },
            selectCsrq () {
                this.birthPicker = true;
            },
            selectXb () {
                this.xbPicker = true;
            },
            onChangeXb (picker, value, index) {
                this.xb = value;
            }
        }
    }
</script>


