import { getUserName } from '../api/axios.js';
export default {
    setUserName: ({ commit, state}) => {
        state.isloading = true;
        getUserName({name: 'admin',password: 'admin123'}).then((res) => {
            commit('SETUSERNAME', 'admin') 
        });
    }
}


